///*--------------------------------------------*\
//    # INDIUM USED MOSTLY FOR TOPIC COLLECTIONS
//\*--------------------------------------------*/


.indium__summary{
  color: $summary-copy;
}

.indium--wrap{
  background-color: #f2f2f2;
  padding-top: 16px;
  padding-bottom:16px;
}
.indium__lead-promo{
  @include flex(1, 1, 100%);
   @include flex-direction(row);
  .indium__media{
    @include mq($from:gel-bp-xxl) {
    display: inline-block;
    float: right;
   }
  }
  .indium__body{
    display: inline-block;
    vertical-align: top;
     @include mq($from:gel-bp-m, $until:gel-bp-l) {
      .gel-double-pica-bold{font-size:18px; line-height:22px; letter-spacing:normal;}
    }
  }
}

  .indium--slot{
    background-color: #fff;
    @include flex-display;
    @include flex(1, 1, 100%);
    flex-direction: column;
  }
  .indium__body{
    @include flex-display;
    @include flex(1, 1, 100%);
    flex-direction: column;
  }
  .indium__meta{
    position: absolute;
    bottom: 0;
  }
